.form {
    background-color: #989810;
    color: #f7efef;
    border-radius: 2rem;
    font-size: 2rem;
    padding: 1rem;
    width: 85%;
    height: 200px;
    margin-top: 20px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    transition-property: box-shadow;

}

.form:hover{
    box-shadow: 3px 3px 8px 5px #777720 ;
}

.label{
    float: left;
    margin-left: 1rem;
    margin-bottom: 0.8rem;
    
}

.input {
    float: left;
    height: 100%;
    width: 90%;
    padding: 1rem;
    font-size: larger;
}

.button {
    background-color: rgb(228, 222, 148);
    color: white;
    float: right;
    padding: 0.6rem;
    border: none;
 
}
.button:hover{
    background-color: rgb(167, 161, 101);
}

.colorboxcontainer{
    display: flex;
    flex-direction: column;
    margin-right: 1rem;
    

}

.colorchoosebox {
    width: 2rem;
    height: 2rem;
    padding: 0.4rem;
    text-align: center;

}

.alist{
    background-color: #ff7707;
    
}

.alist:hover{
    background-color: #ce650f;
    box-shadow: 0 0 2px 1px #c55e09;
}


.blist{
    background-color: #f8b436;
}

.blist:hover{
    background-color: #bd8d35;
    box-shadow: 0 0 2px 1px #bd8d35;
}


.clist {
    background-color: #e9e2a4;
}

.clist:hover{
    background-color: #a7a172;
    box-shadow: 0 0 2px 1px #aca675;
}
